import ResourceCategories from './resourcecatgories';
import ResourceButtons from "./resourcebuttons";

export default class ResourceSection extends React.Component {
    render() {
        return (
            <React.Fragment>
                <section className="resources-filters">
                    <ResourceButtons data={this.props.data} />
                </section>
                <button onClick={this.props.clickButton}>Hello</button>
                <section className="resources-container">
                    {console.log(this.props)}
                    {this.props.resources.map(item => (
                        <a href={item.permalink} className="resource-item " key={item.id}>
                            <div>
                                <h1>{item.title}</h1>
                                <ResourceCategories data={item.terms} />
                            </div>
                        </a>
                    ))}
                </section>
            </React.Fragment>
        )
    }
}